@mixin row($gutter: $grid-gutter-width){
    display: flex;
    flex-wrap: wrap;
    margin-left: -$gutter / 2;
    margin-right: -$gutter / 2;
}
@mixin col-default($gutter: $grid-gutter-width){
    position: relative;
    width: 100%;
    padding-left: $gutter / 2;
    padding-right: $gutter / 2;
}
@mixin col($size, $col: $grid-columns){
    flex: 0 0 percentage($size / $col);
    max-width: percentage($size / $col);
}
@each $breakpoint-name, $breakpoint in $grid-breakpoints{
    // 做出 rwd container
    @each $width-name, $max-width in $container-max-widths{
        @if ($breakpoint-name == $width-name){
            @include rwd($breakpoint){
                .container{
                    width: 100%;
                    padding-left: $grid-gutter-width / 2;
                    padding-right: $grid-gutter-width / 2;
                    margin-left: auto;
                    margin-right: auto;
                    max-width: $max-width;
                }
            }
        }
    }
    // 做出 rwd row + col
    @include rwd($breakpoint){
        .row-#{$breakpoint-name}{
            @include row;
        }
        @if ($grid-columns >0){
            @for $i from 1 through $grid-columns{
                .col-#{$breakpoint-name}-#{$i}{
                    @include col-default;
                    @include col($i);
                }
            }
        }
    }
}