// margin & padding
@each $name, $spacer in $spacers{
    .mt-#{$name}{
        margin-top: $spacer;
    }
    .mb-#{$name}{
        margin-bottom: $spacer;
    }
    .ml-#{$name}{
        margin-left: $spacer;
    }
    .mr-#{$name}{
        margin-right: $spacer;
    }
    .mx-#{$name}{
        margin-left: $spacer;
        margin-right: $spacer;
    }
    .my-#{$name}{
        margin-top: $spacer;
        margin-bottom: $spacer;
    }
    .pt-#{$name}{
        padding-top: $spacer;
    }
    .pb-#{$name}{
        padding-bottom: $spacer;
    }
    .pl-#{$name}{
        padding-left: $spacer;
    }
    .pr-#{$name}{
        padding-right: $spacer;
    }
    .px-#{$name}{
        padding-left: $spacer;
        padding-right: $spacer;
    }
    .py-#{$name}{
        padding-top: $spacer;
        padding-bottom: $spacer;
    }
}