// colors
$primary: #000;
$secondary: #855CE5;
$dark-secondary: #6231BC;
$danger: #F9AEE1;
$white: #fff;

$colors: () !default;
$colors: map-merge(
    (
        "primary": $primary,
        "secondary": $secondary,
        "dark-secondary": $dark-secondary,
        "danger": $danger,
        "white": $white,
    ),
    $colors
);


// spacer
$spacer: 1rem !default;
$spacers: () !default;
$spacers: map-merge(
    (
        auto: auto,
        0: 0,
        1: ($spacer * .5), //8px,
        2: ($spacer * .75), //12px,
        3: ($spacer * 1), //16px,
        4: ($spacer * 1.25), //20px,
        5: ($spacer * 1.5), //24px,
        6: ($spacer * 1.875), //30px,
        7: ($spacer * 2), //32px,
        8: ($spacer * 2.5), //40px,
        9: ($spacer * 3), //48px,
        10: ($spacer * 3.75), //60px,
        11: ($spacer * 4), //64px,
        12: ($spacer * 5), //80px,
        13: ($spacer * 6.25), //100px,
    ),
    $spacers
);

// grid & rwd
$grid-breakpoints:(
    sm: 576px,
    md: 768px,
    lg: 992px,
) !default;

$grid-columns: 12 !default;
$grid-gutter-width: 30px !default;

$container-max-widths: (
    sm: 540px,
    md: 720px,
    lg: 960px,
) !default;